<template>
  <HelloPage ref="hello"></HelloPage>
  <button @click="call" >Call api</button>

  <div v-for="post in posts" :key="post.id">
    <h3>{{ post.titles }}</h3>
    <img v-bind:src="post.images" alt="">
    <p>{{ post.detail_content }}</p>
  </div>

</template>

<script>
import HelloPage from './components/HelloPage.vue';

export default {
  name: 'App',
  components: {
    HelloPage
  },
  data(){
    return{
      posts: [],
    };
  },

  methods: {
    alert(){
      console.log('Ok Ok')
      this.$refs.hello.gang()
    },

    call() {
    fetch("https://63b3e7ccea89e3e3db50b5fb.mockapi.io/api/posts")
      .then((res) => res.json())
      .then((posts) => {
        this.posts = posts;
        console.log("Data detail:", posts);
      });
  },
   
  }
}

</script>

